<template>
  <div id="app">
    <div class="header">
      <div class="header_wrap wrap">
        <a href="#home" class="header_left"></a>
        <div class="header_center">
          <a :class="{header_center_a: true, active: activeName == 'home'}" @click="menuClick('home')" href="#home">{{english ? 'Home' : ''}}</a>
          <a :class="{header_center_a: true, active: activeName == 'obj'}" @click="menuClick('obj')" href="#obj">{{english ? 'Rating Project' : ''}}</a>
          <a :class="{header_center_a: true, active: activeName == 'contact'}" @click="menuClick('contact')" href="#contact">{{english ? 'Contact' : ''}}</a>
        </div>
        <a href="javascript:;" @click="changeLan" class="header_right">/EN</a>
      </div>
    </div>
    <div id="home" class="home">
      <div class="wrap">
        <div class="home_box">
          <div class="home_box_name"></div>
          <div class="home_box_subname"></div>
          <div class="home_box_line"></div>
          <div class="home_box_slogan">
            ..
          </div>
        </div>
      </div>
    </div>
    <div id="obj" class="obj">
      <div class="wrap">
        <div class="obj_box">
          <div class="obj_box_name"></div>
          <div class="obj_box_slogan">
            
          </div>
        </div>
      </div>
    </div>
    <div id="contact" class="contact">
      <div class="contact-left"></div>
      <div class="contact-right"></div>
      <div class="wrap">
        <div class="contact_box">
          <div class="contact_box_name">
            
          </div>
          <div class="contact_box_con">
            <div class="contact_box_left">
              <div class="contact_box_weixin"></div>
              <div class="contact_box_weixin_foot">
                <div class="icon_wrap">
                  <div class="icon_l icon_l_weixin"></div>
                  <div class="icon_r"></div>
                </div>
              </div>
            </div>
            <div class="contact_box_right">
              <div class="contact_box_right-t">
                <div class="icon_wrap">
                  <div class="icon_l icon_l_weibo"></div>
                  <div class="icon_r"></div>
                </div>
                <a href="https://weibo.com" class="icon_link">https://weibo.com</a>
              </div>
              <div class="contact_box_right-b">
                <div class="icon_wrap">
                  <div class="icon_l icon_l_youxiang"></div>
                  <div class="icon_r"></div>
                </div>
                <a href="https://zhenlian@163.com" class="icon_link">https://zhenlian@163.com</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <div class="wrap">
        <div class="footer_box">2018 ZhenLian ICP030173 </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      activeName: window.location.hash.substr(1,) || 'home',
      english: false,
    }
  },
  methods: {
    menuClick(name) {
      this.activeName = name;
    },
    changeLan() {
      this.english = !this.english;
    }
  }
}
</script>

<style lang="scss">
@import "./reset.scss";
.app_wrapper {
  width: 990px;
  margin: 0 auto;
  min-height: calc(100vh - 277px);
}
.header {
  width: 100%;
  height: 80px;
  background: #202020;
  color: #fff;
  &_left {
    font-weight: 500;
    float: left;
    font-size: 32px;
    padding-right: 190px;
    display: block;
  }
  &_center {
    float: left;
    font-size: 18px;
    overflow: hidden;
    &_a {
      display: block;
      float: left;
      margin-right: 50px;
      padding: 0 15px;
      height: 80px;
      &.active {
        border-bottom: 4px solid #0694ea;
      }
      &:hover {
        border-bottom: 4px solid #0694ea;
      }
    }
  }
  &_right {
    float: right;
    font-size: 18px;
    display: block;
  }
}
.header_wrap {
  height: 80px;
  line-height: 80px;
  overflow: hidden;
}
.wrap {
   width: 990px;
  margin: 0 auto;
}

.home {
  height: 537px;
  width: 100%;
  background: url('./assets/bgtop.png') no-repeat center;
  background-size: cover;
  &_box {
    padding-left: 224px;
    padding-top: 154px;
    &_name {
      width: 169px;
      height: 40px;
      background: url('./assets/top-name.png') no-repeat center;
      background-size: cover;
    }
    &_subname {
      font-size: 32px;
      color: #fff;
      font-weight: 300;
      margin-top: 18px;
    }
    &_line {
      width: 60px;
      height: 2px;
      background: #0694ea;
      margin: 38px 0;
    }
    &_slogan {
      color: #fff;
      font-size: 22px;
      width: 240px;
      height: 42px;
      line-height: 42px;
      text-align: center;
      background: #2683ff;
    }
  }
}

.obj {
  height: 700px;
  width: 100%;
  background: url('./assets/bgbottom.png') no-repeat center;
  background-size: cover;
  &_box {
    padding-top: 260px;
    &_name {
      margin: 0 auto;
      width: 332px;
      height: 42px;
      background: url('./assets/bottom-name.png') no-repeat center;
      background-size: 100% 100%;
    }
    &_slogan {
      margin: 60px auto;
      color: #fff;
      font-size: 20px;
      text-align: center;
    }
  }
}

.contact {
  height: 554px;
  width: 100%;
  position: relative;
  &-left {
    position: absolute;
    left: 0;
    width: 27%;
    height: 554px;
    background: url('./assets/contact-left.png') no-repeat right;
    background-size: cover;
  }
  &-right {
    position: absolute;
    right: 0;
    width: 27%;
    height: 554px;
    background: url('./assets/contact-right.png') no-repeat left;
    background-size: cover;
  }
  &_box {
    padding-top: 106px;
     &_name {
      background: url('./assets/line.png') no-repeat center;
      width: 290px;
      height: 42px;
      line-height: 42px;
      text-align: center;
      margin: 0 auto;
     }
     &_con {
      margin: 84px auto;
      width: 900px;
      padding: 0 10px;
      overflow: hidden;
     }
     &_left {
      float: left;
      margin-left: 224px;
     }
     &_right {
      float: left;
      margin-left: 135px;
      overflow: hidden;
      width: 350px;
      padding-top: 6px;
      &-t {
        position: relative;
      }
      &-b {
        margin-top: 8px;
        position: relative;
      }
     }

     &_weixin {
      width: 142px;
      height: 142px;
      background: #e2e3e5;
      margin-bottom: 10px;
     }
  }
}

.icon_wrap {
  overflow: hidden;
  width: 120px;
  padding-bottom: 40px;
  .icon_l {
    float: left;
    width: 38px;
    height: 30px;
    &_weixin {
      background: url('./assets/weixin.png') no-repeat center;
      background-size: cover;
      margin-left: 20px;
    }
    &_weibo {
      width: 38px;
      background: url('./assets/weibo.png') no-repeat center;
      background-size: cover;
    }
    &_youxiang {
      width: 34px;
      height: 26px;
      background: url('./assets/youxiang.png') no-repeat center;
      background-size: 100%;
    }
  }
  .icon_r {
    float: left;
    font-size: 20px;
    margin-left: 12px;
  }
}

.icon_link {
  display: block;
  position: absolute;
  top: 30px;
  font-size: 20px;
  &:hover {
    color: #2683ff;
  }
}

.footer {
  height: 58px;
  background: #626262;
  &_box {
    line-height: 58px;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: #fff;
  }
}
</style>



